	/* Modernized Dark Theme */
	--bgColor: #282c34; /* Darker background */
	--innerMenuBgColor: #21252b; /* Slightly lighter menu bg */
	--lightInnerMenuHoverBgColor: #3a3f4b; /* Hover state */

	--inputBgColor: #3a3f4b; /* Input background */
	--inputFontColor: #abb2bf; /* Lighter input text */

	--fontColor: #abb2bf; /* Main text color */
	--fontWeightedColor: #e6e6e6; /* Brighter text */
	--fontWeakenedColor: #7f848e; /* Dimmer text */

	--buttonFontColor: #e6e6e6; /* Button text */
	--buttonBgColor: #3a3f4b; /* Button background */
	--buttonActiveBgColor: #61dafb; /* Accent color for active/pressed */
	--buttonHoverBgColor: #4b5263; /* Button hover */
	--buttonHoverFontColor: #ffffff; /* Brighter hover text */

	--borderColor: #21252b; /* Darker border, matching menu bg */
	--accentColor: #61dafb; /* Primary accent color (e.g., for highlights, selections) */
}

html,
body {
	font-family: "Source Sans Pro", sans-serif !important;
	color: var(--fontColor);
	width: 100%;
	height: 100%;
	/* min-width: 1040px; */
	min-height: 100%;
}
body {
	overflow-y: hidden;
	overflow-x: auto;
	margin: 0;
	padding: 0;
}
.pcr-app:not(.visible) {
	display: none;
}
.navbar {
	box-sizing: border-box;
	transition: all 0.4s ease-out;
	max-height: 150px;
	background-color: var(--bgColor);
	padding: 0.5em;
}
#minimizeMenu {
	position: absolute;
	right: 5px;
	transition: all 0.4s ease-out;
	cursor: pointer;
}
.zoomGroup {
	position: absolute;
	left: 0px;
	bottom: 0px;
}
.zoomGroup:hover,
.pianoCanvas:hover ~ .zoomGroup {
	z-index: 1055;
}
canvas {
	z-index: -5;
}
#progressBarCanvas {
	transition: all 0.4s ease-out;
	background-color: var(--inputBgColor);
	box-sizing: border-box;
	left: 0px;
	position: absolute;
	border-bottom: 4px solid var(--borderColor);
	float: left;
	cursor: pointer;
	z-index: 10;
}

.row {
	display: flex;
	justify-content: space-between;
	flex-direction: row;
}
.forcedThinButton {
	width: 60px !important;
}
.innerMenuDivsContainer {
	position: relative;
	width: 100%;
	height: 100%;
	min-width: 1040px;
	background-color: rgba(0, 0, 0, 0);
	pointer-events: none;
	overflow: none;
}
.innerMenuDiv {
	margin-top: 24px;
	position: absolute;
	box-sizing: border-box;
	pointer-events: all;
	z-index: 100;

	width: 30%;
	height: 100%;
	min-width: 300px;
	right: 0px;
	padding: 5px;

	background-color: var(--bgColor);
	box-shadow: -3px 3px 10px 1px rgb(0 0 0, 0.5);

	overflow-y: auto;
	overflow-x: hidden;

	transition: all 0.25s ease-out;
}
.innerMenuDiv.hidden {
	/* right: -30%; */
}

.innerMenuContDiv:first-of-type {
	border-top: none;
}
.innerMenuContDiv {
	background-color: var(--innerMenuBgColor);
	background-image: var(--navBackground);
	color: var(--buttonFontColor);
	border-top: 1px solid var(--borderColor);
	border-radius: 2px;
	/* padding: 5px;
	margin: 5px; */
	box-sizing: border-box;
	overflow: hidden;

	transition: 0.3s all ease-out;
}
.innerMenuContDiv.collapsed {
	max-height: 2em;
}
.clickableTitle:not(.glyphicon) {
	position: relative;
	padding-left: 5px;
	height: 2em;
	box-sizing: border-box;
}
.clickableTitle:hover {
	background-color: var(--lightInnerMenuHoverBgColor);
}
.collapserGlyphSpan {
	font-size: 1em;
	position: absolute !important;
	top: 0.5em !important;
	height: 14px;
	right: 8px;
}
.centeredMenuDiv {
	background-color: var(--bgColor);
	max-height: calc(50% - 50px);
	top: 24px;
	padding: 2em;
	position: absolute;
	width: 50%;
	left: 25%;
	overflow-y: auto;
	overflow-x: hidden;
	border-radius: 5px;
	transition: all 0.3s ease-out;
}
.notification {
	background-color: var(--bgColor);
	max-height: calc(50% - 50px);
	top: 30%;
	padding: 30px;
	position: absolute;
	width: 50%;
	left: 25%;
	overflow-y: auto;
	overflow-x: hidden;
	border-radius: 5px;
}
.highlighted {
	animation: pulse 1.5s infinite;
	box-shadow: 0em 0em 5px 5px rgba(0, 0, 0, 0.8);
}

@-webkit-keyframes pulse {
	0% {
		-webkit-box-shadow: 0 0 0 0 rgba(10, 141, 228, 0.4);
	}
	50% {
		-webkit-box-shadow: 0 0 0 10px rgba(10, 141, 228, 0.4);
	}
	100% {
		-webkit-box-shadow: 0 0 0 0 rgba(10, 141, 228, 0.4);
	}
}
@keyframes pulse {
	0% {
		-moz-box-shadow: 0 0 0 0 rgba(10, 141, 228, 0.4);
		box-shadow: 0 0 0 0 rgba(10, 141, 228, 0.4);
	}
	50% {
		-moz-box-shadow: 0 0 0 10px rgba(10, 141, 228, 0.4);
		box-shadow: 0 0 0 10px rgba(10, 141, 228, 0.4);
	}
	100% {
		-moz-box-shadow: 0 0 0 0 rgba(10, 141, 228, 0.4);
		box-shadow: 0 0 0 0 rgba(10, 141, 228, 0.4);
	}
}

.centeredBigText {
	width: 100%;
	text-align: center;
	margin-top: 1em;
	margin-bottom: 1em;
}
.trackName {
	width: 100%;
	padding-top: 0.1em;
	font-size: 0.8em;
}
.instrumentName {
	width: 100%;
	text-align: left;
	font-size: 0.7em;
	padding-left: 0.2em;
}
.divider {
	border: 0;
	border: 1px solid var(--buttonColor);
}

.floatSpanLeft span {
	float: left;
	margin-right: 2px;
}
.container {
	width: 100% !important;
	display: flex;
}
.halfContainer {
	width: 50% !important;
	float: left;
}
.row {
	width: 100%;
	margin: 0;
}
.col {
	justify-content: space-between !important;
}
.btn-group {
	align-self: auto;
}
.btn-group-vertical {
	display: flex;
	flex-direction: column;
}
.btn {
	position: relative;
	box-sizing: border-box;

	padding: 0.6em 1em; /* Slightly more padding */

	text-align: center;
	font-size: 1em;
	font-weight: normal !important;

	color: var(--buttonFontColor);
	background-color: var(--buttonBgColor);

	outline: none;
	border-radius: 4px; /* Add rounded corners */
	border: 1px solid transparent; /* Use transparent border for consistent size */

	transition: all 0.15s ease-out;

	cursor: pointer; /* Ensure cursor changes */
	-ms-user-select: none; /* Internet Explorer/Edge */
	user-select: none;
}
.btn:hover:active {
	background-color: var(--buttonActiveBgColor);
	color: #1a1a1d; /* Darker text on active accent */
}
.btn:hover {
	background-color: var(--buttonHoverBgColor);
	color: var(--buttonHoverFontColor);
	border-color: var(--buttonHoverBgColor); /* Make border match bg on hover */
}
.btn-select-line {
	border-bottom: 4px solid var(--accentColor); /* Use accent color */
	transition: all 0.15s ease-out;
	position: absolute;
	bottom: 0;
	left: 0;
	width: 0%;
	opacity: 0;
}
.btn.selected {
	background-color: var(--buttonHoverBgColor); /* Maintain hover style when selected */
	color: var(--buttonHoverFontColor);
	border-bottom-left-radius: 0px;
	border-bottom-right-radius: 0px;
}
.btn-lg {
	font-size: 1.5em;
	margin-left: 0.2em;
	margin-right: 0.2em;
}
.btn span {
	font-size: 1em;
}
.pcr-button {
	opacity: 0;
	position: absolute;
}

.topContainer {
	flex: 1;
	display: flex;
	justify-content: center;
	align-items: center;
}

.topContainer:first-child {
	margin-right: auto;
	justify-content: space-between !important;
	align-items: unset;
}

.topContainer:last-child {
	margin-left: auto;
	justify-content: space-between !important;
	align-items: unset;
}

.vertical-align {
	display: flex;
	align-items: center;
}
.hidden {
	visibility: hidden;
	opacity: 0;
	pointer-events: none;
}
.unhidden {
	visibility: visible;
	opacity: 1;
}
.fullscreen {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}
.fullscreen p {
	color: var(--inputBgColor);
	text-align: center;
	position: fixed;
	z-index: 999;
	overflow: show;
	margin: auto;
	top: 11.5em;
	font-size: 1.5em;
	left: 0;
	bottom: 0;
	right: 0;
	height: 50px;
}

.floatLeft {
	float: left;
}
.loadingDiv {
	text-align: left;
}
/* Loading Spinner */
.loader,
.loader:after {
	position: fixed;
	z-index: 999;
	overflow: show;
	margin: auto;
	top: 150px;
	left: 0;
	bottom: 0;
	right: 0;
	width: 50px;
	height: 50px;
	border-radius: 50%;
	width: 10em;
	height: 10em;
}
.loader {
	font-size: 0.5em;
	border-top: 1.1em solid rgba(255, 255, 255, 0.2);
	border-right: 1.1em solid rgba(255, 255, 255, 0.2);
	border-bottom: 1.1em solid rgba(255, 255, 255, 0.2);
	border-left: 1.1em solid #ffffff;
	-webkit-transform: translateZ(0);
	-ms-transform: translateZ(0);
	transform: translateZ(0);
	-webkit-animation: load8 1.1s infinite linear;
	animation: load8 1.1s infinite linear;
}
@-webkit-keyframes load8 {
	0% {
		-webkit-transform: rotate(0deg);
		transform: rotate(0deg);
	}
	100% {
		-webkit-transform: rotate(360deg);
		transform: rotate(360deg);
	}
}
@keyframes load8 {
	0% {
		-webkit-transform: rotate(0deg);
		transform: rotate(0deg);
	}
	100% {
		-webkit-transform: rotate(360deg);
		transform: rotate(360deg);
	}
}

/* --- Responsive Styles --- */
@media (max-width: 768px) {
	.innerMenuDiv {
		width: 100%; /* Take full width on smaller screens */
		min-width: unset; /* Remove the fixed min-width */
		right: 0; /* Ensure it's positioned correctly */
		/* Add other adjustments as needed, e.g., position, height */
	}

	/* Add other rules for small screens here */
}

/* --- Panel Close Button --- */
.panel-close-button {
	position: absolute;
	top: 5px; /* Adjust for potential parent padding */
	right: 5px; /* Adjust for potential parent padding */
	background: yellow !important; /* TEMP: Force visibility */
	color: red !important; /* TEMP: Force visibility */
	border: 1px solid red !important; /* TEMP: Force visibility */
	font-size: 1.5em; /* Slightly smaller */
	font-weight: bold;
	cursor: pointer;
	padding: 0 8px; /* More padding */
	line-height: 1;
	z-index: 101; /* Ensure it's above other panel content */
	transition: color 0.15s ease-out;
}
.panel-close-button:hover {
	color: darkred !important; /* TEMP */
}

/* --- Sheet Music Container --- */
#sheetMusicContainer {
	width: 100%;
	height: 220px; /* Initial height, match VexFlow height + padding */
	padding: 10px;
	box-sizing: border-box;
	background-color: var(--innerMenuBgColor); /* Match theme bg */
	border-top: 1px solid var(--borderColor);
	overflow: hidden; /* Hide potential VexFlow overflow */
	position: relative; /* For potential absolute positioning inside */
}
