___CSS_LOADER_EXPORT___.push([module.id, `.settingsContainer {
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	overflow-y: hidden;
	overflow-x: hidden;
}
.settingsTabButtonContainer {
	background-color: var(--innerMenuBgColor);
	width: 100%;
	top: 0;
	left: 0;
	height: 2em;
	display: flex;
	justify-content: space-between;
	position: absolute;
}

.settingsTabButton {
	flex: 1 1 auto;
	height: 2em;
	padding: 0.6em 1em;
	box-sizing: border-box;
	border: none;
	border-bottom: 2px solid transparent;
	background-color: var(--innerMenuBgColor);
	color: var(--fontColor);
	font-size: 0.9em;
	text-align: center;
	cursor: pointer;
	transition: background-color 0.15s ease-out, border-color 0.15s ease-out, color 0.15s ease-out;
	border-radius: 0;
}

.settingsTabButton:hover {
	background-color: var(--lightInnerMenuHoverBgColor);
	color: var(--buttonHoverFontColor);
}

.settingsTabButton.selected {
	background-color: var(--innerMenuBgColor);
	color: var(--fontWeightedColor);
	border-bottom-color: var(--accentColor);
}

.settingsContentContainer {
	height: 100%;
	overflow-y: hidden;
	overflow-x: hidden;
}
.settingsTabContentContainer {
	overflow-y: auto;
	overflow-x: hidden;
	margin-top: 2em;
	display: none;
	height: calc(100% - 2em);
}
.settingsTabContentContainer button {
	margin: 1.5em;
}
.settingsGroupContainer {
	border-top: 1px solid var(--borderColor);
	padding-top: 0.8em;
	margin-bottom: 0.8em;
}

.settingsGroupContainer:first-of-type {
	border-top: none;
	padding-top: 0;
}
.settingsGroupLabel {
	font-size: 0.9em;
	font-weight: 600;
	padding-left: 0.8em;
	padding-bottom: 0.6em;
	color: var(--fontWeightedColor);
}
.settingContainer {
	padding: 0.8em 1em;
	width: 100%;
	box-sizing: border-box;
	background-color: var(--innerMenuBgColor);
	transition: background-color 0.15s ease-out;
}
.settingContainer:hover {
	background-color: var(--lightInnerMenuHoverBgColor);
}
